import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';

import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

import { CoreCommonModule } from '@core/common.module';

import { AuthLoginComponent } from 'app/layout/pages/authentication/login/auth-login.component';
import { AuthRegisterComponent } from './register/auth-register.component';
import { TranslateModule } from '@ngx-translate/core';

// routing
const routes: Routes = [
  {
    path: 'authentication/login',
    component: AuthLoginComponent,
    data: { animation: 'auth' }
  },
  {
    path: 'authentication/register',
    component: AuthRegisterComponent,
    data: { animation: 'auth' }
  },

];

@NgModule({
  declarations: [AuthLoginComponent,AuthRegisterComponent],
  imports: [CommonModule, RouterModule.forChild(routes),
     NgbModule, FormsModule, ReactiveFormsModule, CoreCommonModule,
     TranslateModule
    ]
})
export class AuthenticationModule {}
