import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { takeUntil, first } from 'rxjs/operators';
import { Subject } from 'rxjs';

import { AuthenticationService } from 'app/auth/service';
import { CoreConfigService } from '@core/services/config.service';
import { User } from 'app/models';

@Component({
  selector: 'app-auth-register.',
  templateUrl: './auth-register.component.html',
  styleUrls: ['./auth-register.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AuthRegisterComponent implements OnInit {
 // Public
 public coreConfig: any;
 public passwordTextType: boolean;
 public registerForm: UntypedFormGroup;
 public submitted = false;
 public error = '';
 public loading = false;
 // Private
 private _unsubscribeAll: Subject<any>;

 /**
  * Constructor
  *
  * @param {CoreConfigService} _coreConfigService
  * @param {FormBuilder} _formBuilder
  */
 constructor(private _coreConfigService: CoreConfigService, 
  private _formBuilder: UntypedFormBuilder,
  private _route: ActivatedRoute,
  private _router: Router,
  private _authenticationService: AuthenticationService
  ) {
   this._unsubscribeAll = new Subject();

   // Configure the layout
   this._coreConfigService.config = {
     layout: {
       navbar: {
         hidden: true
       },
       menu: {
         hidden: true
       },
       footer: {
         hidden: true
       },
       customizer: false,
       enableLocalStorage: false
     }
   };
 }

 // convenience getter for easy access to form fields
 get f() {
   return this.registerForm.controls;
 }

 /**
  * Toggle password
  */
 togglePasswordTextType() {
   this.passwordTextType = !this.passwordTextType;
 }

 /**
  * On Submit
  */
 onSubmit() {
   this.submitted = true;

   // stop here if form is invalid
   if (this.registerForm.invalid) {
     return;
   }
   const user: User = {
      name: this.f.fullname.value,
      email: this.f.email.value,
      role: this.f.userRole.value,
      password:this.f.password.value
  };
  this._registerUser(user);
 }

 private _registerUser(user: User){
  this._authenticationService.register(user).subscribe(
    (user: User) => {
      this.loading = true;

      this._authenticationService.login(this.f.email.value, this.f.password.value)
      .pipe(first()).subscribe(
        data => {
          this._router.navigate(['/']);
        },
        error => {
          this.error = error;
          this.loading = false;
        }
      );
    }, error =>{
      console.log(error.error.message);
  this.error = error.error.message;
    }
  );
}


 // Lifecycle Hooks
 // -----------------------------------------------------------------------------------------------------

 /**
  * On init
  */
 ngOnInit(): void {
   this.registerForm = this._formBuilder.group({
     fullname: ['', [Validators.required]],
     email: ['', [Validators.required, Validators.email]],
     password: ['', Validators.required],
     userRole: ['', Validators.required],
     agreed: [false, Validators.requiredTrue]

   });

   // Subscribe to config changes
   this._coreConfigService.config.pipe(takeUntil(this._unsubscribeAll)).subscribe(config => {
     this.coreConfig = config;
   });
 }

 /**
  * On destroy
  */
 ngOnDestroy(): void {
   // Unsubscribe from all subscriptions
   this._unsubscribeAll.next();
   this._unsubscribeAll.complete();
 }
}
